#ghibili {
  margin: 0 auto;
  box-sizing: border-box;
  background: #f6f6f6;
  color: #666666;
  font-family: "Dosis", sans-serif;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* screen / card responsive design */ }
  #ghibili .container {
    margin: 0 auto;
    max-width: 1200px;
    display: flex;
    flex-wrap: wrap;
    /* card float effect with shadow */
    /* 
     * deflt div 1 : pink-blue      1 3 7 9
     * if div by 2 : green-blue     2 6
     * if div by 4 : pink-pink      4 8
     * if div by 5 : coral-pink     5 10
     */ }
    #ghibili .container img {
      display: block;
      margin: 1rem auto;
      max-width: 100%; }
    #ghibili .container .card:hover {
      box-shadow: 2px 8px 45px rgba(0, 0, 0, 0.15);
      -webkit-transform: translate3D(0, -2px, 0);
              transform: translate3D(0, -2px, 0); }
    #ghibili .container .card {
      margin: 1rem;
      background: white;
      box-shadow: 2px 4px 25px rgba(0, 0, 0, 0.1);
      border-radius: 12px;
      overflow: hidden;
      -webkit-transition: all 0.2s linear;
      transition: all 0.2s linear; }
      #ghibili .container .card p {
        margin: 0;
        padding: 0 2.5rem 2.5rem; }
    #ghibili .container .card h1 {
      margin: 0 0 2rem 0;
      padding: 1.5rem 2.5rem;
      background-image: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
      color: white;
      font-size: 1.5rem;
      text-align: center; }
    #ghibili .container .card:nth-child(2n) h1 {
      background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%); }
    #ghibili .container .card:nth-child(4n) h1 {
      background-image: linear-gradient(120deg, #ff9a9e 0%, #fecfef 100%); }
    #ghibili .container .card:nth-child(5n) h1 {
      background-image: linear-gradient(120deg, #ffc3a0 0%, #ffafbd 100%); }
  @media only screen and (min-width: 600px) {
    #ghibili .container > .card {
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: calc(50% - 2rem); } }
  @media only screen and (min-width: 900px) {
    #ghibili .container > .card {
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: calc(33% - 2rem); } }

